.main {
	padding: 25px 0;
}

.cart {
	overflow: hidden;
	height: 430px;
	padding: 10px;
	border: 1px solid #ccc;
	border-radius: 8px;
	margin-bottom: 15px;
	&__img-wrapper {
		width: 70%;
		margin: 0 auto 10px;
	}
	&__img {
		width: 100%;
		height: 250px;
		margin: 0 auto;
	}
	&__title {
		@include font(700, 1.2rem, 1rem, $font-family);
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		margin-bottom: 8px;
	}
	&__description {
		@include font(400, 0.8rem, 1rem, $font-family);
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		height: 25px;
		margin-bottom: 8px;
	}
	&__price {
		@include font(700, 1.2rem, 1rem, $font-family);
		margin-bottom: 15px;
	}
	&__buy[data-inCart="false"] {
		display: none;
	}
	&__buy[data-inCart="true"] {
		margin: 0 auto;
		max-width: 200px;
		display: flex;
		align-items: center;
		justify-content: center;
		margin-bottom: 10px;
	}
	&__buy-remove {
		width: 25px;
		height: 25px;
		text-align: center;
		background-color: rgb(255, 4, 4);
		color: #fff;
		padding: 3px 0px;
		@include font(700, 16px, 1rem, $font-family);
		border-radius: 3px;
		border: 1px solid #000;
		margin-right: 15px;
		cursor: pointer;
	}
	&__buy-count {
		width: 25px;
		height: 25px;
		text-align: center;
		padding-top: 5px;
		color: #000;
		@include font(700, 16px, 1rem, $font-family);
		border-radius: 3px;
		border: 1px solid #000;
	}
	&__buy-add {
		cursor: pointer;
		width: 25px;
		height: 25px;
		text-align: center;
		background-color: rgb(109, 255, 4);
		color: #fff;
		padding: 5px 3px;
		@include font(700, 16px, 1rem, $font-family);
		border-radius: 3px;
		border: 1px solid #000;
		margin-left: 15px;
	}
	&__add {
		@include font(700, 1rem, 1rem, $font-family);
		width: 100%;
		background-color: rgba(0, 203, 0, 0.954);
		padding: 5px;
		text-align: center;
		color: #fff;
		border-radius: 8px;
		cursor: pointer;
	}
}
